.login>.button:hover {
    opacity: 0.25;
}

.hide-img {
    width: 1.5rem;
}

.white-img {
    width: 1.5rem;
    margin-left: 1rem;
}

.pass-span {
    margin-left: 1rem;
}

@media only screen and (max-width: 913px) {
    .login {
        width: auto;
        margin: 2rem;
        margin-top: 7rem;
    }

