:root {
    --main-color: #ecf0f1;
    --point-color: #555;
    --size: 5px;
  }
  
  .loader {
    background-color: #ecf0f1;
    background-color: var(--main-color);
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0; left: 0;
    display: flex;
    align-items: center;
    align-content: center; 
    justify-content: center;  
    z-index: 100000;
  }
  
  .loader__element {
    border-radius: 100%;
    border: 5px solid #555;
    border: var(--size) solid var(--point-color);
    margin: calc(5px*2);
    margin: calc(var(--size)*2);
  }
  
  .loader__element:nth-child(1) {
    -webkit-animation: preloader .6s ease-in-out alternate infinite;
            animation: preloader .6s ease-in-out alternate infinite;
  }
  .loader__element:nth-child(2) {
    -webkit-animation: preloader .6s ease-in-out alternate .2s infinite;
            animation: preloader .6s ease-in-out alternate .2s infinite;
  }
  
  .loader__element:nth-child(3) {
    -webkit-animation: preloader .6s ease-in-out alternate .4s infinite;
            animation: preloader .6s ease-in-out alternate .4s infinite;
  }
  
  @-webkit-keyframes preloader {
    100% { -webkit-transform: scale(2); transform: scale(2); }
  }
  
  @keyframes preloader {
    100% { -webkit-transform: scale(2); transform: scale(2); }
  }
